@import "../../globalSASS/global.scss";

.gallery {
	width: 95vw;
	max-width: 1280px;
	height: 72.8vh;
	
    overflow-y: scroll;
    
	&__wrap {
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    
    &__card {
        position: relative;
        width: 380px;
        overflow: hidden;
        
    }

    &__container {
        position: relative;
        width: 100%;

        &:hover {
            .gallery__svg {
                transform: translate(0, 0);
            }
        }
    }

    &__viewport {
        width: 100%;
        overflow: hidden;
    }

    &__slider {
        display: flex;
        width: 100%;
        height: 250px;

        transition: transform .5s;
    }

    &__img {
        width: 100%;
        object-fit: cover;
        object-position: 0 0;
    }

    &__svg {
        position: absolute;
        top: 110px;

        width: 30px;
        height: 30px;

        z-index: 2;
        transition: 0.3s;

        cursor: pointer;

        &_arrow_l {
            left: 10px;
            transform: translate(-150%, 0);
        }
        &_arrow_r {
            right: 10px;
            transform: translate(150%, 0);
        }
    }

    &__adress {
        margin: 0;
        margin-top: 10px;

        font-size: 20 / 16 + rem;
        font-weight: bold;
    }

    &__text {
        margin: 0;
        margin-top: 10px;
        margin-bottom: 20px;
    }

    &__button {
        font-size: 1rem;
        width: 200px;
        height: 40px;
        margin-left: 50%;
        transform: translate(-50%, 0);
        margin-bottom: 20px;
    }

    @media screen and (max-width:1250px) {
        width: 830px;
    }

    @media screen and (max-width:1023px) and (min-width:767px) {
        width: 630px;

        &__wrap {
            justify-content: center;
        }

        &__card {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
        }

        &__container {
            display: flex;
            justify-content: center;
        }

        &__viewport {
            width: 490px;
        }

        &__slider {
            height: 320px;
        }

       &__svg {
           top: 140px;
           width: 50px;
           height: 50px;

           &_arrow_l {
            left: 0;
           }

           &_arrow_r {
            right: 0;
           }
       }

       &__adress {
           margin-top: 20px;
           font-size: 25 / 16 + rem;
           width: 490px;
       }
       &__text {
           margin-bottom: 27px;
           font-size: 20 / 16 + rem;
           width: 490px;
           
       }

       &__button {
        font-size: 25 / 16 + rem;
        width: 300px;
        height: 60px;
        margin-bottom: 50px;
       }
    }

    @media screen and (max-width:767px) {
        width: 100%;
        max-width: 490px;
        height: 67.8vh;

        &__card {
            width: 100%;
        }

        &__adress {
            margin-left: 17px;
        }

        &__text {
            margin-left: 17px;
        }
    }
}
