@import "../../globalSASS/global.scss";
.header {
	position: relative;
	// overflow: hidden;

	overflow-x: hidden;

	&_theme_room {
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		background-image: url('img/room.jpg');
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

		// height: 770px;
		height: 100vh;

	}

	&_pages {
		height: 109px;
		overflow: visible;

		@media screen and (max-width:767px) {
			height: 38.6vw;
			max-height: 240px;
		}
	}

	&__bar {
		padding-top: 22px;
	}

	&__burger {
		position: absolute;
		top: 6.4%;
		right: 0%;

		transform: translate(-42%, -50%);
		
		&_pages {
			top: 25%;
		}
	}

	&__calculator {
		display: none;
		position: absolute;
		top: 50%;
		left: 50%;

		transform: translate(-50%, -50%);

	}

	&__call {
		padding-top: 10px;

	}

	&__menu {
		top: 87px;

	}

	&__mobile-mask {
		position: absolute;

		width: 100%;
		height: 100%;

		background-color: rgba($color: #fff, $alpha: 0.8);


		z-index: 10;
	}



	@media screen and (max-height: 760px) {
		&__calculator {
			// top: 0;
			// transform: translate(-50%, 0);

			// margin-top: 105px;

			top: 58%;
		}
	}



	@media screen and (max-width: 767px) {

		.header {
			&__bar {
				padding-top: 12px;
				margin-left: 4.6%;
			}

			&__calculator {
				top: 0;

				transform: translate(-50%, 0);
			}


		}
	}

}
