@import "../../globalSASS/global.scss";
.button {

    font-family: 'Alegreya Sans', sans-serif;

	background: $colorYellow;
	border-radius: 100px;
    border: none;
    
    padding: (16 / 25 + em) (45 / 25 + em);

    transition: 0.3s;
    cursor: pointer;

    outline: none;

    -webkit-tap-highlight-color: rgba($color: #000000, $alpha: 0);
    
    &:hover {
        box-shadow: 0 0 0 (17 / 25 + em) rgba($color: $colorYellow, $alpha: 0.4);
    }
} 
