@import "../../globalSASS/global.scss";
.bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 95vw;
    max-width: 930px;

    margin-left: auto;
    margin-right: auto;

    &__logo {
        width: 130px;
    }

    @media screen and (max-width: 767px){
        flex-direction: column;
        margin: 0;
        align-items: flex-start;
        justify-content: start ;
        width: 43.75vw;

        &__logo {
            width: 31.25vw;
            max-width: 200px;
        }
    }
}