@import "../../globalSASS/global.scss";
.about {

    &__title {
        padding-top: 126px;
    }

    &__description {
        
        display: flex;
        align-items: center;
        justify-content: space-between;

        width: 815px;
        margin-left: 50%;
        margin-top: 11px;

        transform: translate(-56.3%, 0);
    }

    &__text {
        margin: 0;
        margin-top: 12px;
        width: 456px;

        font-size: 17 / 16 + rem;
    }

    @media screen and (max-width:950px) {
       
        &__description {
            width: 707px;
        }

        &__text {
            width: 364px;
        }
    }

    @media screen and (max-width:767px) {
        display: none;
    }

}