@import "../../globalSASS/global.scss";
.call {
	display: flex;
	align-items: center;

	&__phone {
		padding-right: 7%;
	}

	&__call-back {
		padding: (6 / 16 + em) (22 / 16 + em);
        
        font-family: 'Alegreya Sans', sans-serif;
        font-size: 1rem;

        background: $colorYellow;
        border-radius: 20px;
		border: none;
		transition: 0.3s;
		white-space: nowrap;
		
		cursor: pointer;
		
		outline: none;
		-webkit-tap-highlight-color: rgba($color: #000000, $alpha: 0);

		&:hover {
			box-shadow: 0 0 0 (12 / 25 + em) rgba($color: $colorYellow, $alpha: 0.4);
		}
	}

	@media screen and (max-width: 767px){
		flex-direction: column;
		align-items: flex-start;
		.call {
			&__phone {
				padding-right: 0;
				margin-bottom: 0.25em;

				font-size: 40px;
			}

			&__call-back {
				font-size: 32px;
				font-weight: lighter;
			}
		}
	}
	
	@media screen and (max-width: 640px){
		flex-direction: column;
		
		.call {
			&__phone {
				font-size: 6.25vw;
			}

			&__call-back {
				font-size: 5vw;
			}
		}
    }
}
