@import "../../globalSASS/global.scss";
.menu {
	position: absolute;
	left: 50%;

	transform: translate(-50%, 0);
	width: 100%;

	z-index: 11;

	&_scroll {
		top: 0;
		position: fixed;
		background-color: rgba($color: #fff, $alpha: 0.9);
		color: #fff;
		transition: all .3s ease;
	}


	&_out {
		transform: translate(-50%, -100%);
	}

	&__wrap {
		display: flex;
		justify-content: space-between;

		width: 569px;
		margin-left: auto;
		margin-right: auto;
	}

	&__item {
		display: flex;
		position: relative;
		overflow: hidden;


		&:hover:before {
			height: 3px;
		}

		&::before {
			position: absolute;
			content: '';
			top: 50%;
			left: 0;

			transition: 0.3s;
			transform: translate(0, -50%);

			height: 0;
			width: 100%;

			background-color: $colorYellow;
		}
	}

	&__link {
		position: relative;
		color: #000;
		text-decoration: none;
		font-size: 18 / 16 + rem;
		z-index: 2;
	}

	
	@media screen and (max-width:767px){
		display: none;
		position: absolute;
		
		top: 11%;
		left: 0;
		transform: translate(0,0);
		height: 30vh;
		max-height: 240px;

		background-color: rgba($color: #fff, $alpha: 0.9);

		&__pages {
			top: 40%;
		}

		.menu {

			

			&__wrap {
				position: relative;
				margin-right: 5%;

				width: 80%;
				height: 100%;

				flex-direction: column;
				justify-content: space-around;
				align-items: flex-end;


			}

			&__link {
				font-size: 28.8px;
			}
		}


	}

	@media screen and (max-width: 640px){
		
		.menu__link {
			font-size: 3.6vh;
		}
	}
}
