@charset "UTF-8";
.main {
  overflow: hidden; }
  .main__questions {
    position: absolute;
    left: 50%;
    top: 51%;
    transform: translate(-50%, -50%); }
  .main__contacts {
    position: absolute;
    left: 50%;
    top: 51%;
    transform: translate(-50%, -50%); }
  .main__services {
    margin-top: 6.6vh;
    margin-left: 50%;
    transform: translate(-50%, 0); }
    @media screen and (max-width: 1023px) {
      .main__services {
        margin-top: 3vh; } }
  .main__gallery {
    margin-top: 58px;
    margin-left: auto;
    margin-right: auto; }
    @media screen and (max-width: 1023px) {
      .main__gallery {
        margin-top: 80px; } }
    @media screen and (max-height: 913px) and (min-width: 767px) {
      .main__gallery {
        margin-top: 5%; } }
    @media screen and (max-height: 768px) and (min-width: 767px) {
      .main__gallery {
        margin-top: 2%; } }
    @media screen and (max-width: 767px) {
      .main__gallery {
        margin-top: 45px; } }
  @media screen and (max-width: 767px) {
    .main__page_contacts {
      height: 750px; } }
  @media screen and (max-width: 767px) {
    .main__page_faq {
      min-height: 80vh;
      height: auto; } }
  .main_hide-scroll ::-webkit-scrollbar {
    /* chrome based */
    width: 0px;
    /* ширина scrollbar'a */
    background: transparent;
    /* опционально */ }
  @media screen and (max-width: 767px) {
    .main__contacts {
      transform: translate(-50%, 0);
      top: auto;
      left: 50%;
      margin-top: 88px; }
    .main__questions {
      position: static;
      transform: translate(-50%, 0);
      margin-left: 50%; } }
