@import "../../globalSASS/global.scss";
.to-action {
	position: absolute;
	top: 56.3%;
	left: 50%;

	transform: translate(-50%, -50%);

	width: 80vw;
	max-width: 930px;

	&__title {
		margin: 0;
		font-family: 'Bebas Neue', cursive;
		font-weight: normal;
		font-size: 60 / 16 + rem;
		text-transform: uppercase;

		// @media screen and (max-width: 1162.5px) {
		// 	font-size: 5.16vw;
		// }
	}

	&__subtitle {
		margin: 0;
		font-size: 30 / 16 + rem;
		font-weight: normal;

		margin-top: 1.2%;

		// @media screen and (max-width: 1162.5px) {
		// 	font-size: 2.58vw;
		// }
	}

	&__button {
		font-size: 25 / 16 + rem;
		margin-top: 68 / 25 + em;
		

		// @media screen and (max-width: 1162.5px) {
		// 	font-size: 2.15vw;
		// }
	}


	@media screen and (max-width:767px) {
		top: 30%;
		left: 4.6%;

		transform: translate(0, 0);
		width: 100%;

		.to-action {

			&__title {
				font-size: 60px;
			}

			&__subtitle {
				font-size: 40px;
				margin-top: 2.8%;
			}

			&__button {
				position: absolute;
				top: 110%;
				left: 45.4%;
                transform: translate(-50%, 0);
                
                padding: (10 / 16 + em) (29 / 16 + em);

                font-size: 36px;
                font-weight: lighter;
			}
		}
	}

	@media screen and (max-width:640px) {
		.to-action {

			&__title {
				font-size: 9.375vw;
			}

			&__subtitle {
				font-size: 6.25vw;
            }
            
            &__button {
				font-size: 5.625vw;
			}
		}

	}
}