@import "../../globalSASS/global.scss";
.way {

    &__title {
        padding-top: 126px;
    }

    &__route {
        position: relative;
        width: 759px;
        height: 410px;

        margin-left: auto;
        margin-right: auto;
        margin-top: 44px;
    }

    &__step {
        position: absolute;
        display: flex;
        align-items: center;

        &_request {
            top: 0;
            left: 0;
        }

        &_meet {
            top: 30%;
            left: 53%;
        }

        &_estimate {
            top: 56%;
            left: 0;
        }

        &_sign {
            top: 80%;
            left: 54%;
        }
    }

    &__img {
        margin-right: 20px;
    }

    &__text {
        font-size: 20 / 16 + rem;
    }

    &__line {
        position: absolute;

        &_1 {
            top: 17%;
            left: 4%;

            width: 414.5px;
            height: 79px;       
        }

        &_2 {
            top: 48%;
            left: 3%;

            width: 418.6px;
            height: 53.5px;
        }

        &_3 {
            top: 75%;
            left: 2.4%;

            width: 388.6px;
            height: 56.5px;
        }

        
    }

    @media screen and (max-width:767px) {
        
        .way{
            &__title {
                padding-top: 100px;
                font-size: 30px;
            }

            &__route {
                width: 250px;
                height: auto;
            }

            &__step {
                position: relative;
                top: 0;
                left: 0;
                margin-top: 32px;

                flex-direction: column;
            }

            &__img {
                margin: 0;
            }

            &__text {
                margin: 0;
                margin-top: 6px;

                width: 250px;

                font-size: 1rem;
                text-align: center;
                
                
            }

            &__line {
                display: none;
                width: 1px;
            }
        }
    }

    @media screen and (max-width:385px) {
        .way{
            &__title { 
                font-size: 7.8vw;
            }
        }
    }
}