@import "../../globalSASS/global.scss";
.calculator {
	width: 930px;
	height: 420px;

	background-color: rgba($color: #fff, $alpha: 0.7);

	z-index: 15;



	&__wrap {
		position: relative;
		margin-top: 26px;
		margin-left: 50%;
		transform: translate(-50%, 0);
		width: 96%;
		height: 90%;
	}

	&__close {
		position: absolute;
		right: 0;
		top: -2%;

		font-size: 25 / 16 + rem;

		cursor: pointer;

		&:hover {
			color: rgb(83, 83, 83);
		}
	}

	&__content {
		margin-top: 46px;
	}

	&__form {
		display: flex;
		flex-direction: column;
	}

	&__side-top {
		display: flex;
		width: 100%;

	}

	&__housing {
		display: flex;
		flex-direction: column;
		margin-left: 3.5%;
	}

	&__subtitle {
		font-size: 20 / 16 + rem;

		&_ceiling {
			margin-top: 8px;
		}
	}

	&__choice {
		margin-top: 20px;
	}

	&__check {
		display: flex;

		font-size: 20 / 16 + rem;

		&:not(:first-child) {
			margin-top: 11px;
		}

		&_grid {

			&:not(:first-child) {
				margin-top: 0;
			}
		}

		&_politic {
			margin-left: 30px;
		}
	}

	&__radio {
		position: absolute;
		opacity: 0;
		z-index: -1;
	}

	&__name {
		position: relative;
		padding: 0 0 0 25px;

		cursor: pointer;

		&:before {
			content: '';
			position: absolute;
			top: 0px;
			left: 0;
			width: 18px;
			height: 18px;
			border: 1px solid #C4C4C4;
			border-radius: 2px;
			background: #FFF;
		}

		&:after {
			content: '';
			position: absolute;
			top: 1px;
			left: 1px;
			width: 18px;
			height: 18px;
			border-radius: 2px;
			background: $colorYellow;
			opacity: 0;
			transition: .2s;
		}

		&_politic {
			font-size: 1rem;
			padding-left: 30px;

			&:before {
				top: 9px;
			}

			&:after {
				top: 10px;
			}
		}
	}

	.calculator__radio:checked+.calculator__name:after {
		opacity: 1;
	}

	&__rooms {
		display: flex;
		flex-direction: column;
		margin-left: 3%;
	}

	&__quantity {
		display: flex;
		align-items: center;
		margin-top: 16px;
		margin-left: 50%;

		transform: translate(-50%, 0);
	}

	&__svg {
		width: 15px;
		height: 15px;

		cursor: pointer;

		&_uncurrent {
			--primary-color: #C4C4C4;
			cursor: default;
		}
	}

	&__room {
		margin-left: 8px;
		margin-right: 8px;

		width: 24px;


		font-family: 'Alegreya Sans', sans-serif;
		font-size: 20 / 16 + rem;
		text-align: center;

		background-color: transparent;
		border: none;

		outline: none;
	}

	&__necessary {
		margin-left: 4.5%;
	}

	&__grid {
		display: grid;
		grid-template-columns: repeat(2, 165px);
		grid-template-rows: repeat(4, 22px);

		grid-row-gap: 10px;
		grid-column-gap: 26px;

		grid-auto-flow: column;

	}

	&__entry {
		position: absolute;
		left: 84%;
		top: 30%;
		display: flex;
		flex-direction: column;
	}

	&__data {
		padding-left: 10px;
		width: 70px;
		height: 30px;

		font-family: 'Alegreya Sans', sans-serif;
		font-size: 20 / 16 + rem;

		border-radius: 100px;
		border: 1px solid #C4C4C4;

		outline: none;

		&:focus {
			border-color: $colorYellow;
        }
	}

	&__side-bot {
		margin-top: 34px;
		margin-left: 52px;
		display: flex;
		align-items: flex-end;
	}

	&__user {
		height: 40px;
		width: 180px;
		padding-left: 20px;


		font-family: 'Alegreya Sans', sans-serif;
		font-size: 20 / 16 + rem;

		border: none;
		border-radius: 100px;

		background-color: #dadada;

		outline: none;


		&:focus {
			box-shadow: 0 0 1px 1px $colorYellow;
        }
	}

	&__phone {
		margin-left: 28px;

		height: 40px;
		width: 180px;
		padding-left: 20px;

		font-family: 'Alegreya Sans', sans-serif;
		font-size: 20 / 16 + rem;

		border: none;
		border-radius: 100px;

		background-color: #dadada;

		outline: none;

		&:focus {
			box-shadow: 0 0 1px 1px $colorYellow;
        }
	}

	

	@media screen and (max-width:950px) and (min-width: 767px) {
		width: 720px;
		height: 540px;


		&__wrap {
			margin-top: 31px;
		}

		&__housing {
			margin-left: 0;
		}

		&__rooms {
			margin-left: 30px;
			white-space: nowrap;
		}

		&__necessary {
			margin-left: 38px;
		}

		&__grid {
			grid-row-gap: 12px;
		}

		&__entry {
			top: 156px;
			left: 160px;
		}

		&__subtitle {
			&_area {
				margin-top: 6px;
				order: 3;
			}

			&_ceiling {
				margin-top: 0;
				order: 1;
			}
		}

		&__data {
			&_area {
				order: 4;
			}

			&_ceiling {
				order: 2;
			}
		}

		&__name {
			&_politic {}
		}

		&__check {


			&_politic {

				&:not(:first-child) {
					width: 333px;
					margin-left: auto;
					margin-right: auto;
					margin-top: 18px;
				}
			}
		}

		&__side-bot {
			margin-left: auto;
			margin-right: auto;
			margin-top: 72px;
			width: 430px;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		&__phone {
			margin: 0;
		}

		button.calculator__button {
			margin-top: 32px;
		}
	}

	@media screen and (max-height: 680px) and (min-width: 767px){

		height: 480px;

		&__wrap {
			margin-top: 15px;
		}

		&__content {
			margin-top: 20px;
		}

		button.calculator__button {
			margin-top: 15px;
		}
	}

	@media screen and (max-width: 767px) {
		width: 100%;
		height: auto;
		background-color: rgba($color: #fff, $alpha: 0.9);

		.calculator {

			

			&__wrap {
				margin-top: 22px;
				margin-left: 50%;
				transform: translate(-50%, 0);

				max-width: 330px;
			}

			&__close {
				font-size: 30 / 16 + rem;

				right: 2%;
				top: -1%;
			}

			&__title {
				font-size: 25 / 16 + rem;
			}

			&__content {
				margin-top: 32px;
			}

			&__side-top {
				flex-direction: column;
			}

			&__entry {
				right: 2%;
				left: auto;
				top: 58px;

				width: 80px;
			}

			&__subtitle {

				&:not(:first-child) {
					margin-top: 1px;
				}
			}

			&__rooms {
				margin-top: 40px;
				margin-left: 0;

				.calculator__subtitle {
					text-align: center;
				}
			}

			&__quantity {
				margin-top: 10px;
				width: 103px;
			}

			&__svg {
				width: 30px;
				height: 30px;
			}

			&__necessary {
				margin-top: 30px;
				margin-left: 3.5%;
			}

			&__grid {
				grid-template-columns: repeat(1, 1fr);
				grid-template-rows: repeat(8, 22px);
				grid-row-gap: 11.5px;
			}

			&__side-bot {
				margin-top: 32px;
				margin-left: 0;
				flex-direction: column;
				align-items: center;

			}

			&__phone {
				margin-left: 0;
				margin-top: 17px;
			}

			&__name {

				&_politic {
					display: none;
				}
			}

			&__button {
				margin-top: 8px;
				margin-bottom: 20px;
			}
		}
	}


}

button.calculator__button {
	font-size: 20 / 16 + rem;
	width: 200px;
	height: 40px;

	margin-left: 50%;
	margin-top: 20px;

	padding: 0;


	transform: translate(-50%, 0);
}
