@import "../../globalSASS/global.scss";
.main {
	&__questions {
		position: absolute;
		left: 50%;
		top: 51%;

		transform: translate(-50%, -50%);

	}

	&__contacts {
		position: absolute;
		left: 50%;
		top: 51%;

		transform: translate(-50%, -50%);
	}

	&__services {
		margin-top: 6.6vh;
		margin-left: 50%;
		transform: translate(-50%, 0);

		@media screen and (max-width:1023px) {
			margin-top: 3vh;
		}

		
	}

	&__gallery {
		margin-top: 58px;
		margin-left: auto;
		margin-right: auto;

		@media screen and (max-width:1023px) {
			margin-top: 80px;
		}

		@media screen and (max-height: 913px) and (min-width: 767px) {
			margin-top: 5%;
		}
		@media screen and (max-height: 768px) and (min-width: 767px) {
			margin-top: 2%;
		}

		@media screen and (max-width:767px) {
			margin-top: 45px;
		}
	}

	&__page {
		&_contacts {
			@media screen and (max-width:767px) {
				height: 750px;
			}
		}

		&_faq {
			@media screen and (max-width:767px) {
				min-height: 80vh;
				height: auto;
			}
		}
	}

	&_hide-scroll {
		::-webkit-scrollbar {
			/* chrome based */
			width: 0px;
			/* ширина scrollbar'a */
			background: transparent;
			/* опционально */
		}
	}

	overflow: hidden;

	@media screen and (max-width:767px) {
		&__contacts {
			transform: translate(-50%,0);
			top: auto;
			left: 50%;
			margin-top: 88px;
		}

		&__questions {
			position: static;
			transform: translate(-50%,0);
			margin-left: 50%;
		}
	}
}
