@import "../../globalSASS/global.scss";
.contacts {
	width: 80vw;
	max-width: 930px;
	height: 390px;

	background: rgba(255, 255, 255, 0.7);

	&__wrap {
		position: relative;
		width: 100%;
		height: 100%;
	}

	&__content {
		position: absolute;
		margin-top: 40px;
		left: 50%;
		transform: translate(-56%, 0);

		display: flex;
		width: 637px;

		@media screen and (max-width:930px) {
			transform: translate(-46.5%, 0);
		}
	}

	&__map {
		width: 300px;
		height: 255px;
	}

	&__info {
		margin-left: 100px;
		width: 250px;

		@media screen and (max-width: 860px) {
			margin-left: 50px;
		}
	}

	&__text {
		margin: 0;
		font-size: 20 / 16 + rem;

	}

	&__time {
		display: flex;
		flex-direction: column;
		margin-top: 10px;
	}

	&__subtitle {
		font-size: 17 / 16 + rem;
		font-weight: bold;

		flex-grow: 1;
	}

	&__items {
		display: flex;
		flex-direction: column;
		width: 163px;
	}

	&__item {
		display: flex;
		justify-content: space-between;
		width: 100%;
		font-size: 17 / 16 + rem;

	}

	&__call {
		margin-top: 12px;
		display: flex;
		align-items: center;
	}

	&__svg {
		width: 30px;
		height: 30px;
	}

	&__phones {
		margin-left: 12px;

		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	&__mail {
		margin-top: 12px;
		display: flex;
		align-items: center;
	}

	&__link {
		text-decoration: none;
		font-size: 20 / 16 + rem;
		color: #000;

		&_mail {
			margin-left: 12px;
			font-size: 17 / 16 + rem;
		}
	}

	@media screen and (max-width:767px) {
		min-width: 320px;
		width: 70vw;
        height: 642px;
        
        &__title {
            position: absolute;
            left: 50%;
            top: -10.5%;
            
            transform: translate(-50%, 0);
        }

		&__content {
            margin-top: 20px;
            transform: translate(-50%, 0);
            flex-direction: column;
            width: 300px;
		}

		&__map {
			width: 300px;
        }
        
        &__info {
            margin-top: 65px;
            margin-left: 34px;
        }
	}

}

h1.contacts__title {
	margin: 0;
	margin-top: 20px;
}
