@import "../../globalSASS/global.scss";
.services {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	width: 90vw;
	max-width: 880px;
	min-width: 810px;

	&__carousel {
		width: 100%;
		height: auto;
	}

	&__wrap {
		position: relative;
		width: 100%;
		height: auto;

		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}


	&__card {
		position: relative;
		display: flex;
		flex-direction: column;
		width: 260px;

		background-color: rgba($color: #fff, $alpha: 0.8);
		margin-bottom: 60px;
	}

	&__header {
		position: relative;

		width: 100%;
		height: 117px;

	}

	&__title {
		position: absolute;
		top: 25%;
		left: 50%;
		transform: translate(-50%, 0);


		font-family: 'Bebas Neue', sans-serif;
		text-transform: uppercase;
		font-size: 20 / 16 + rem;
		color: $colorYellow;
		white-space: nowrap;
	}

	&__subtitle {
		margin: 0;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, 0);

		font-size: 20 / 16 + rem;
		color: $colorYellow;
		white-space: nowrap;

		&_l {
			font-size: 24 / 20 + em;
		}
	}


	&__list {
		margin-left: 17px;
		margin-top: 20px;

		display: flex;
		flex-direction: column;

		padding-bottom: 18px;

		font-size: 14 / 16 + rem;
	}

	&__item {
		position: relative;
		margin-left: 15px;

		line-height: 17px;

		&:not(:first-child) {
			margin-top: 12px;
		}

		&:before {
			position: absolute;
			top: 50%;
			left: -15px;

			transform: translate(0, -50%);

			content: '';
			display: block;
			height: 5px;
			width: 5px;

			background-color: $colorYellow;
		}
	}

	&__button {
		position: absolute;
		bottom: -55px;
		left: 50%;
		transform: translate(-50%, 0);

		width: 200px;
		height: 40px;

		font-size: 1rem;
		white-space: nowrap;
	}

	&__svg {
		position: absolute;
		display: none;

		width: 50px;
		height: 50px;

		cursor: pointer;

		&_arrow_l {
			top: 40%;
			left: 11%;
		}

		&_arrow_r {
			top: 40%;
			right: 11%;
		}
	}


	@media screen and (max-width:1023px) and (min-width:768px) {

		&__header {
			height: 19.04vh;
		}

		&__title {
			font-size: 3.2vh;
		}

		&__subtitle {
			font-size: 3.2vh;
		}

		&__img {
			width: 100%;
		}

		&__item {
			font-size: 1.95vh;
			margin-left: 3.42vh;

			&:not(:first-child) {
				margin-top: 2.54vh;
			}

			&:before {
				left: -1.95vh;
				height: 0.78vh;
				width: 0.78vh;
			}
		}

		&__list {
			margin-top: 2.735vh;
			padding-bottom: 2.54vh;
		}

		&__carousel {
			margin-left: auto;
			margin-right: auto;

			width: 43.95vh;


			overflow: hidden;
		}



		&__wrap {
			display: flex;
			align-items: center;
			width: 42vh;
			padding-left: 0.98vh;
			padding-right: 0.98vh;

			transition: transform .5s;
			z-index: 1;
		}

		&__card {
			flex: 0 0 42vh;
			margin-bottom: 14.65vh;
			margin-left: 0.78vh;
			margin-right: 0.78vh;
		}

		&__svg {
			display: block;
			width: 4.88vh;
			height: 4.88vh;
		}

		&__button {
			bottom: -8vh;

			width: 29.3vh;
			height: 5.86vh;

			font-size: 2.44vh;
		}
	}

	@media screen and (max-width:1023px) and (max-height: 925px) and (min-width:768px) {

		&__header {
			height: 17.136vh;
		}

		&__title {
			font-size: 2.88vh;
		}

		&__subtitle {
			font-size: 2.88vh;
		}

		&__img {
			width: 100%;
		}

		&__item {
			font-size: 1.755vh;
			margin-left: 3.078vh;

			&:not(:first-child) {
				margin-top: 2.286vh;
			}

			&:before {
				left: -1.755vh;
				height: 0.702vh;
				width: 0.702vh;
			}
		}

		&__list {
			margin-top: 2.4615vh;
			padding-bottom: 2.286vh;
		}

		&__carousel {
			margin-left: auto;
			margin-right: auto;

			width: 39.555vh;


			overflow: hidden;
		}



		&__wrap {
			display: flex;
			align-items: center;
			width: 37.8vh;
			padding-left: 0.882vh;
			padding-right: 0.882vh;

			transition: transform .5s;
			z-index: 1;
		}

		&__card {
			flex: 0 0 37.8vh;
			margin-bottom: 13.185vh;
			margin-left: 0.702vh;
			margin-right: 0.702vh;
		}

		&__svg {
			display: block;

			&_arrow_l {
				left: 20%;
			}
			&_arrow_r {
				right: 20%;
			}
		}

		&__button {
			bottom: -7.2vh;

			width: 26.37vh;
			height: 5.274vh;

			font-size: 2.196vh;
		}
	}

	@media screen and (max-width:767px) {

		width: 100%;
		min-width: auto;
		
		&__wrap {
			display: flex;
			align-items: center;
			

			transition: transform .5s;
			z-index: 1;
		}

		&__carousel {
			margin-left: auto;
			margin-right: auto;

			width: 260px;


			overflow: hidden;
		}

		&__svg {
			display: block;
			z-index: 3;

			&_arrow_l {
				left: 50%;
				transform: translate(-155px, 0);
			}

			&_arrow_r {
				left: 50%;
				transform: translate(105px, 0);
			}
		}
	}

}
