@import "../../globalSASS/global.scss";
.burger {
	

	display: flex;
	flex-direction: column;
	justify-content: space-between;

	width: 10.9vw;
	height: 6.25vw;
	max-width: 70px;
	max-height: 40px;

	cursor: pointer;

	-webkit-tap-highlight-color: rgba($color: #000000, $alpha: 0);


	&__close {

		.burger {

			&__line {
				&:nth-child(1) {
					transform: translate(0%, -12.48px) rotate(-45deg);
					transform-origin: right top;
				}

				&:nth-child(2) {
					transform: scale(0);
				}

				&:nth-child(3) {
					transform: translate(0, 0) rotate(45deg);
					transform-origin: right bottom;
				}
			}
		}

		@media screen and (max-width:640px) {

			.burger {
	
				&__line {
					&:nth-child(1) {
						transform: translate(0%, -1.95vw) rotate(-45deg);
					}
	
					&:nth-child(2) {
						transform: scale(0);
					}
	
					&:nth-child(3) {
						transform: translate(0, 0) rotate(45deg);
					}
				}
			}
		}
	}

	&__line {
		display: block;
		height: 10%;
		min-height: 2px;
		width: 100%;

		background-color: #000;

		transition: all 0.3s;

		white-space: nowrap;
	}

	@media screen and (min-width: 768px) {
		display: none;
	}

	


}
