@import "../../globalSASS/global.scss";
.accordeon {
    width: 80%;
    margin-top: 22px;
    margin-bottom: 32px;

	&__item {
        display: flex;
        position: relative;
        
        margin-top: 20px;

    }
    
    &__button {
        width: 30px;
        height: 30px;

        min-width: 30px;

        border: 1px solid #000;
        background-color: #fff;

        cursor: pointer;
        outline: none;
    }

    &__wrap {
        margin-left: 18px;
    }

	&__question {
        margin: 0;
        
        font-size: 20 / 16 + rem;
        font-weight: normal;
        
		
	}

	&__answer {
        margin: 0;
        margin-top: 10px;
        display: none;
    }
    
    @media screen and (max-width:767px) {
        width: 95%;
        margin-bottom: 0;
        padding-bottom: 32px;
    }
  
}

