@import "../../globalSASS/global.scss";
.sale {

	&_popup {
		display: none;
		position: absolute;
		top: 50%;
		left: 50%;

		transform: translate(-50%, -50%);
		z-index: 3;
	}

	&__wrap {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;

		width: 930px;
		height: 249px;

		margin-left: auto;
		margin-right: auto;

		background-color: $colorYellow;
	}

	&__box {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 97%;
		height: 88%;

		background-color: #fff;
	}

	&__content {
		display: flex;
		width: 95%;
		height: 77%;
	}

	&__img {
		height: 100%;
	}

	&__side-r {
		flex-grow: 1;
		height: 100%;
	}

	&__title {
		margin: 0;
		margin-top: 5px;
	}

	&__form {
		display: flex;
        width: 100%;
        margin-top: 40px;
	}

	&__input {
		width: 185px;
        height: 40px;
        
        font-size: 20 / 16 + rem;
        font-family: 'Alegreya Sans', sans-serif;

		background: #F2F2F2;
		border-radius: 100px;
        border: none;
        
        margin-right: 20px;
        padding-left: 15px;
        outline: none;

        transition: 0.2s;

        &:focus {
            box-shadow: 0 0 1px 1px $colorYellow;
        }
	}
	
	&__check {
		position: absolute;
		top: 67.5%;
		right: 11.5%;

		display: flex;
	}

	&__radio {
		position: absolute;
		opacity: 0;
		z-index: -1;
	}

	&__name {

		position: relative;
		padding: 0 0 0 25px;
		width: 330px;

		cursor: pointer;

		&:before {
			content: '';
			position: absolute;
			top: 9px;
			left: 0;
			width: 18px;
			height: 18px;
			border: 1px solid #C4C4C4;
			border-radius: 2px;
			background: #FFF;
		}

		&:after {
			content: '';
			position: absolute;
			top: 10px;
			left: 1px;
			width: 18px;
			height: 18px;
			border-radius: 2px;
			background: $colorYellow;
			opacity: 0;
			transition: .2s;
		}
	}

	.sale__radio:checked+.sale__name:after {
		opacity: 1;
	}
    
    &__button {
        font-size: 20 / 16 + rem;
        padding: 0.5rem 4rem;
	}

	@media screen and (max-width:950px) {
		
		&__wrap {
			width: 700px;
		}

		&__img {
			position: absolute;
			height: 169px;
		}

		&__title {
			position: relative;
			z-index: 2;
		}

		&__form {
			position: relative;
			z-index: 2;
			justify-content: space-between;

			
		}

		&__input {
			margin: 0;
			margin-right: 18px;
		}

		&__check {
			top: auto;
			bottom: -50px;
			right: 10%;
		}
	}
	
	@media screen and (max-width:767px) {
		margin-top: 69px;

		.sale {
			&__wrap {
				width: 90.6vw;
				height: 377px;
			}

			&__box {
				
				width: 89.5%;
				height: 92%;
			}

			&__content {
				position: relative;
				height: 94.6%;
			}

			&__img {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 202px;
				height: auto;
			}

			&__side-r {
				position: relative;
				width: 100%;
				height: 100%;
				z-index: 2;
			}

			&__title {
				width: 70%;
				margin-left: auto;
				margin-right: auto;
				margin-top: 0;
			}

			&__form {
				position: absolute;
				top: 50%;
				left: 50%;

				transform: translate(-50%, -48%);
				flex-direction: column;
				align-items: center;
			}

			&__input {
				margin: 0;
				margin-left: 50%;
				transform: translate(-50%, 0);

				&:not(:first-child) {
					margin-top: 20px;
				}
			}

			&__check {
				display: none;
			}

			&__button {
				margin-top: 65px;
				margin-left: 50%;
				transform: translate(-50%, 0);
			}
		}
	}
}
