@import "../../globalSASS/global.scss";
.advantages {
	position: relative;
	height: 610px;

	&__title {
		padding-top: 128px;
	}

	&__examples {
		// position: absolute;
		// left: 50%;
		// top: 50%;

		// transform: translate(-25%, -50%);

		margin-left: auto;
		margin-right: auto;
		margin-top: 66px;
		width: 725px;

		display: grid;
		grid-template-columns: 322px 357px;
		grid-template-rows: repeat(3, 80px);
		grid-row-gap: 10px;
		grid-column-gap: 62px;

	}

	&__item {
		display: flex;
		align-items: center;
	}

	&__icon {
		width: 60px;
		margin-right: 5%;
	}

	&__description {
		margin: 0;
		font-size: 20 / 16 + rem;
	}

	&__strong {
		font-size: 26 / 16 + rem;
	}

	@media screen and (max-width: 767px) {

		height: 100%;

		.advantages {
			&__title {
				padding-top: 34px;
				font-size: 25 / 16 + rem;
			}

			&__examples {

				margin-top: 40px;
				width: 260px;

				grid-template-columns: 100%;
				grid-template-rows: 116px;
				grid-row-gap: 30px;
				grid-column-gap: 0;
			}

			&__item {
				flex-direction: column;
			}

			&__description {
				margin-top: 10px;
				text-align: center;
				width: 100%;
			}
		}
	}
}
