@import "../../globalSASS/global.scss";
.questions {
    width: 80vw;
    max-width: 930px;

    background: rgba(255, 255, 255, 0.7);

    &__title {
        margin: 0;
        margin-top: 24px;
    }

    &__accordeon {
        margin-left: auto;
        margin-right: auto;
    }

    @media screen and (max-width:767px) {
        width: 100%;
        margin-bottom: 20px;

        &__title {
            padding-top: 24px;
        }
    }
}