@import "../../globalSASS/global.scss";
.footer {
	display: flex;
	align-items: flex-end;
	justify-content: space-between;

	width: 95vw;
	max-width: 930px;

	margin-left: auto;
	margin-right: auto;


	&_main {
		padding-top: 112px;
		padding-bottom: 28px;



		@media screen and (max-width: 860px) {
			position: relative;
			width: 90vw;
			height: 96px;
			margin-bottom: 20px;

			.footer {



				&__logo {
					position: absolute;
					bottom: 0;
					left: 0;
				}

				&__link {
					position: absolute;
					word-wrap: none;
					white-space: nowrap;
					transform: translate(-50%, 0);
					left: 50%;

					&_confid {
						bottom: 20%;
					}

					&_agree {
						bottom: 0;
					}

				}

				&__phone {
					position: absolute;
					bottom: 0;
					right: 0;
				}
			}
		}

		@media screen and (max-width:767px) {
			padding: 0;
			margin-top: 80px;
		}

		@media screen and (max-width:600px) {

			.footer {
				&__link {

					&_confid {
						top: 0;
					}

					&_agree {
						top: 23%;
					}
				}
			}
		}
	}

	&_contacts {
		@media screen and (max-width:767px) {
			margin-top: 25px;
			margin-bottom: 20px;
		}
	}


	&__logo {
		width: 128px;
	}

	&__link {
		text-decoration: none;
		color: #000;
	}

	@media screen and (min-width:768px)  {
		&_faq {
			position: absolute;
			bottom: 1.5%;
			left: 50%;
			padding-top: 0;
			padding-bottom: 0;
			margin-bottom: 0;
			transform: translate(-50%, 0);
	
		}
	}

}
